// Here you can add other styles
@import "scss/_variables.scss";

button:focus {
  outline: 1px auto -webkit-focus-ring-color;
  outline: none;
}
button {
  text-align: inherit;
  padding: initial;
}
main {
  height: 100%;
}
hr {
  height: 0;
  border-top: 1px solid #ccc;
  margin: 0 auto;
  width: 80%;
  display: block;
  margin-top: -1px;
  z-index: 1;
}
.pointer {
  cursor: pointer;
}
.breadcrumb {
  margin-bottom: 0;
}
.main .container-fluid {
  padding: 10px 30px;
}
.img-avatar {
  border-radius: 4px;
}


body {
  font-family: roboto;
  background: #fff;
  color: $text;
}
div, main, button {
  display: block;
  box-sizing: border-box;
  line-height: 1;
  position: relative;
}
b {
  font-weight: 600;
  // color: $text-strong;
}
p {
  line-height: 1.5;
}
article {
  padding: 20px 0;
}
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  line-height: 1.3;
  margin: 0;
}
a {
  color: inherit;
  text-decoration: inherit;
}
span, .span, p, .p { font-size: 12px; line-height: 1.3;}
h1, .h1 { font-size: 24px; }
h2, .h2 { font-size: 20px; }
h3, .h3 { font-size: 18px; }
h4, .h4 { font-size: 16px; }
h5, .h5 { font-size: 14px; }
h6, .h6 { font-size: 12px; }
small {
  font-size: 10px;
  line-height: 1;
  margin: 0;
}

.float-right {
  float: right;
}

.mr-1 {
  margin-right: 5px;
}
.ml-1 {
  margin-left: 5px;
}
.pl-1 {
  padding-left: 5px;
}
.m1 {
  margin: 5px;
}
.m2 {
  margin: 10px;
}
.mb-1 {
  margin-bottom: 5px;
}
.my-1 {
  margin-top: 5px;
  margin-bottom: 5px;
}
.my-2 {
  margin-top: 10px;
  margin-bottom: 10px;
}
.p1 {
  padding: 5px;
}
.p2 {
  padding: 10px;
}
.p3 {
  padding: 15px;
}
.pt-1 {
  padding-top: 5px;
}
.pt-2 {
  padding-top: 10px;
}
.pr-1 {
  padding-right: 5px;
}
.pr-2 {
  padding-right: 10px;
}
.py-1 {
  padding-top: 5px;
  padding-bottom: 5px;
}
.py-2 {
  padding-top: 10px;
  padding-bottom: 10px;
}
.py-3 {
  padding-top: 15px;
  padding-bottom: 15px;
}
.px-1 {
  padding-left: 5px;
  padding-right: 5px;
}
.px-2 {
  padding-left: 10px;
  padding-right: 10px;
}
.px-3 {
  padding-left: 15px;
  padding-right: 15px;
}
button {
  font-size: 12px;
}
.icon-lg {
  font-size: 35px;
}

.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}

hr.vertical {
  border: 0;
  width: 0;
  height: 22px;
  border-right: 1px solid #ccc;
  display: inline-block;
  vertical-align: middle;
}
.fullheight {
  height: 100%;
}
.fullwidth {
  width: 100%;
}
.halfwidth {
  width: 50%;
}
.flex-row-parent {
  display: flex;
  flex-direction: row;
  &.flex-middle {
    justify-content: center;
  }
  &.flex-top {
    align-content: flex-start;
  }
  &.flex-center {
    align-items: center;
  }
  &.multiline {
    flex-wrap: wrap;
  }
  .flex-self-center {
    align-self: center;
  }
  &.flex-space-between {
    justify-content: space-between;
  }
  .flex-100 {
    flex: 0 0 100px;
  }
  .flex-80 {
    flex: 0 0 80px;
  }
  .flex-70 {
    flex: 0 0 70px;
  }
  .flex-50 {
    flex: 0 0 50px;
  }
  .flex-40 {
    flex: 0 0 40px;
  }
  .flex-30 {
    flex: 0 0 30px;
  }
}
.flex-expand {
  flex: 1;
}
.flex-column-parent {
  display: flex;
  flex-direction: column;
  &.flex-space-between {
    justify-content: space-between;
  }
}
.overflow-y {
  overflow-y: overlay;
}
.overflow-x {
  overflow-y: overlay;
}
.overflow-overlay {
  overflow: overlay;
}
.nowrap {
  white-space: nowrap;
}
.inline-block-conatiner {
  font-size: 0;
  letter-spacing: 0;
}
.inline-block {
  display: inline-block;
}
.background-cover {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.border-bottom {
  border-bottom: 1px solid #ccc;
}
.arrow-right {
  display: inline-block;
  vertical-align: middle;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 7px solid $text;
  margin: 0 6px;
  &.red { border-left-color: $red; }
  &.green { border-left-color: $green; }
  
}
.arrow-up {
  display: inline-block;
  vertical-align: middle;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 7px solid $text;
  margin: 0 6px;
  &.red { border-bottom-color: $red; }
  &.green { border-bottom-color: $green; }
}
.arrow-down {
  display: inline-block;
  vertical-align: middle;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 7px solid $text;
  margin: 0 6px;
  &.red { border-top-color: $red; }
  &.green { border-top-color: $green; }
}

.red { color: $red; }
.green { color: $green; }
.yellow { color: $yellow; }
.grey { color: $grey; }
.black { color: $black; }
.weak { color: $text-weaken; }
.strong { color: $text-strong; } 

.bg-red { background-color: $red; }
.bg-green { background-color: $green; }
.bg-yellow { background-color: $yellow; }
.bg-grey { background-color: $grey; }
.bg-black { background-color: $black; }
